* {
  box-sizing: border-box;
}
.container_register {
    padding: 16px;
    background-color: white;
    width: auto;
    text-align: left;
    margin: 10% 35%;
    /* margin:0 30%; */
    /* height: 5%; */
  }
  
  /* Full-width input fields */
  input[type=text], input[type=password] {
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    /* display: inline-block; */
    border: none;
    background: #f1f1f1;
  }
  
  input[type=text]:focus, input[type=password]:focus {
    background-color: #ddd;
    outline: none;
  }
  
  /* Overwrite default styles of hr */
  hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
  }
  
  /* Set a style for the submit button */
  .registerbtn {
    background-color: #04AA6D;
    color: white;
    padding: 16px 20px;
    margin: 4px 0;
    border: none;
    cursor: pointer;
    /* width: 15%;  */
    opacity: 0.9;
  }
  
  .registerbtn:hover {
    opacity: 1;
  }
  
  /* Add a blue text color to links */
  a {
    color: dodgerblue;
  }
  
  /* Set a grey background color and center the text of the "sign in" section */
  .signin {
     
    text-align: center;
    margin: 0 5rem;
  }
  .btn_signup{
    width: 5rem;
    height: 1rem;
    border-style: none;
    color: green;
  }
  